.dieWrapper {
  width: 26px;
  height: 26px;
  position: relative;
  -webkit-perspective: 1000px;
          perspective: 1000px;
  margin: 5px;
  display: inline-block;
}

circle {
  stroke: black;
  fill: url(#dieGradient);
}

.die {
  width: 100%;
  height: 100%;
  position: absolute;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transition: -webkit-transform 2s;
  transition: -webkit-transform 2s;
  -o-transition: transform 2s;
  transition: transform 2s;
  transition: transform 2s, -webkit-transform 2s;
}
.die figure {
  margin: 0;
  width: 98%;
  height: 98%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  position: absolute;
  border: 2px solid #232323;
  background: #fff;
  border-radius: 5px;
}
.metGoal circle {
  fill: #fff;
}
.die.metGoal figure {
  background: #ff0000;
}
.die svg {
  width: 100%;
  height: 100%;
}
.die .front{
  -webkit-transform: rotateY(0deg) translateZ(13px);
          transform: rotateY(0deg) translateZ(13px);
}
.die .back{
  -webkit-transform: rotateX(180deg) translateZ(13px);
          transform: rotateX(180deg) translateZ(13px);
}
.die .right{
  -webkit-transform: rotateY(90deg) translateZ(13px);
          transform: rotateY(90deg) translateZ(13px);
}
.die .left{
  -webkit-transform: rotateY(-90deg) translateZ(13px);
          transform: rotateY(-90deg) translateZ(13px);
}
.die .top{
  -webkit-transform: rotateX(90deg) translateZ(13px);
          transform: rotateX(90deg) translateZ(13px);
}
.die .bottom{
  -webkit-transform: rotateX(-90deg) translateZ(13px);
          transform: rotateX(-90deg) translateZ(13px);
}



